* {
  margin: 0;
}

:root {
  --hoverlink: #00acdc;
}

body {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: block;
}

.nav {
  margin-right: 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  font-size: 12px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  margin-right: 20px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__last__link {
  margin-right: 0;
}

.is-active {
  display: inline-block;
  position: relative;
}

.is-active, .nav__link:hover {
  color: var(--hoverlink);
}

.is-active:after {
  content: "";
  border-bottom: 4px solid var(--hoverlink);
  border-radius: 8px;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
